@media screen and (max-width: 650px) {
  .headline {
    font-size: 70%;
  }

  #myleftNavALT {
    display: inline-block;
  }

  .myleftNav {
    display: none;
  }

  .headFormHide {
    display: inline-block;
  }

  .headForm {
    display: none;
    cursor: pointer;
  }

  .leftSideALT {
  width: 100%;
  height: 0vh;
  z-index: 1;
  transition: 0.5s;
  background-color: #d07009;
  overflow-x: hidden; 
}

.leftSideALT a {
  text-decoration: none;
  font-size: 25px;
  display: block;  
  transition: 0.3s;
  color: white;
  transition: 0.5s;
}
  
}

